import styled from 'styled-components';

export const Avatar = styled.img`
  width: 80px;
  border-radius: 200px;
`;

export const Title = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  display: table;
  
  &> * {
    display: table-cell;
    font-size: 24px;
    font-weight: 600;
    vertical-align: middle;
    line-height: 1.3;
  }

  button {
    padding: 5px;
    height: 32px;
    line-height: 0;
    padding-top: 0px;
  }
`;