import React from 'react';
import PropTypes from 'prop-types';
import { Container, Row, Col } from 'reactstrap';

import { Avatar, Title } from './style';

const propTypes = {
  title: PropTypes.string.isRequired,
  avatar: PropTypes.string,
};

export default function TitleSection(props) {
  return (
    <Container className="bordered">
      <Row className="bordered">
        <Col xs="12" className="bordered">
          <Row>
            <Col xs="auto" className="bordered orange">
              <Avatar src={avatar} alt={title} />
            </Col>
            <Col className="bordered orange">
              <Title>
                <span>
                  {title} <button>&#9733;</button>
                </span>
              </Title>
            </Col>
          </Row>
        </Col>

        <Col xs="12" className="bordered">
          Buttns
        </Col>
      </Row>
    </Container>
  );
}

TitleSection.propTypes = propTypes;